body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
        Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #212121;
    line-height: 1.5;
    margin: 0;
    padding: 0;
}

.search-form {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(59, 59, 136);
    padding: 25px 0;
    margin: 0px;
}

.search-form-input {
    width: 300px;
    height: 30px;
    font-size: 16px;
    outline: none;
    border: none;
}

.search-form-button {
    width: 44px;
    height: 32px;
    border: none;
    font-size: 14px;
    cursor: pointer;
    padding: 0;
}

.icon-search {
    text-align: center;
    margin: 5px;
    padding: 0;
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    margin: 20px;
    justify-content: center;
}

.photo-card {
    margin-right: 20px;
}

.photo {
    display: block;
    width: 300px;
    height: 200px;
    object-fit: cover;

}

.info {
    width: 300px;
    border-bottom: 1px solid #c3c2c2;
    border-left: 1px solid #c3c2c2;
    border-right: 1px solid #c3c2c2;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    justify-content: center;
    align-items: center;
    justify-items: center;
    margin-bottom: 20px;
    font-size: 12px;
    display: flex;
    box-shadow: 0 3px 1px rgb(0 0 0 / 10%), 0 1px 2px rgb(0 0 0 / 8%), 0 2px 2px rgb(0 0 0 / 12%);
}

.info_item {
    flex-flow: column wrap;
    align-items: center;
    padding: 0 12px;
    display: flex;
}

.info_item-api {
    padding-top: 5px;
}

.load-more {
    width: 200px;
    height: 40px;
    margin-top: 50px;
    margin-bottom: 30px;
    margin-left: 50%;
    transform: translateX(-50%);
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: rgb(59, 59, 136);
    color: aliceblue
}

